.review {
  display: flex;
  align-items: center;
  color: white;
  margin: 10px 30px;
}
.review-header {
  display: flex;
  align-items: center;
  transform: translateX(100%); /* Animation start out of the screen */

  /* Add the animation */
  animation: move 30s linear infinite
    /* infinite make reapeat the animation indefinitely */;

  white-space: nowrap;
}
.review h2 {
  margin: 0 15px;
  font-size: 18px;
  font-weight: 400;
}
.rating-app {
  background-color: black;
}
.stars {
  display: flex;
  align-items: center;
}
.review p {
  margin: 0;
  font-weight: 200;
  margin-top: 3px;
}
@keyframes move {
  to {
    transform: translateX(-100%);
  }
}
